import * as React from 'react';
import { useDispatch, useSelector } from "react-redux";
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';

import MainLayout from '../Layouts/MainLayout';
import {FormErrorSingleComponent} from '../../Layouts/FormErrorComponent';

import {
  validateUsername,
  validateEmail,
  validatePassword,
} from '../../lib/FormValidationComponent';

import {
  SET_EMAIL,
  SET_PASSWORD,
  SET_EMAIL_VALID,
  SET_EMAIL_NOT_VALID,
  SET_PASSWORD_VALID,
  SET_PASSWORD_NOT_VALID,
} from "../state/auth";

function Login() {
  const dispatch = useDispatch();
  const state = useSelector((state:any)=>state.auth); 
  return (
    <>
      <MainLayout heading={"Login"}>
        <Grid item xs={12} md={12} lg={12} sx={{m:1}}>
          <FormErrorSingleComponent error={(state.email.length > 0) && !state.isValidEmail} msg={"Please provide a valid email address."}>
            <TextField
            sx={{mb: 1}}
              id="email"
              label="Email"
              type="text"
              fullWidth
              error={(state.email.length > 0) && !state.isValidEmail}
              maxRows={4} 
              value={state.email}
              onChange={(e:any)=>{
                if(validateEmail(e.target.value)){
                  dispatch({type:SET_EMAIL_VALID});
                }
                else{
                  dispatch({type:SET_EMAIL_NOT_VALID});
                }
                dispatch({type:SET_EMAIL, payload: {email: e.target.value}});
                return e;
              }}
            />
          </FormErrorSingleComponent>

          <FormErrorSingleComponent error={(state.password.length > 0) && !validateEmail(state.email)} msg={"Password must contain minimum 8 characters with 1 lowercase letter, 1 uppercase letter, 1 number and 1 special character."}>
            <TextField
              id="password"
              label="Password"
              type="password"
              fullWidth
              error={(state.password.length > 0) && !validatePassword(state.password)}
              maxRows={4} 
              onChange={(e:any)=>{
                if(validatePassword(e.target.value)){
                  dispatch({type:SET_PASSWORD_VALID});
                }
                else{
                  dispatch({type:SET_PASSWORD_NOT_VALID});
                }
                dispatch({type:SET_PASSWORD, payload: {password: e.target.value}});
                return e;
              }} 
            />
          </FormErrorSingleComponent>

          <div style={{display: "flex", justifyContent:"center", margin: '20px'}}>
            <Button variant="contained" disabled={!((state.email.length > 0) && (state.password.length > 0) && validateEmail(state.email) && validatePassword(state.password))}>Submit</Button>
          </div>
          

        </Grid>
      </MainLayout>
    </>
  );
}

export default Login;
